@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
body {
  font-family: "Roboto Condensed" sans-serif !important;
  padding: 10px;
}
/******* Navbar *******/
.logo {
  width: 12.25rem;
  height: 12.25rem;
}

/*---------- Menu Links ----------*/
nav a.nav-item {
  font-weight: 500;
  letter-spacing: 0.0375rem;
  cursor: pointer;
  color: #dc7a4a;
  margin-top: 30px;
  vertical-align: baseline;
}
nav ul li {
  text-transform: uppercase;
}
/* #nav-item {
  font-size: 1.5rem;
  margin-top: 1.5rem;
  color: #dc7a4a;
  text-shadow: 1px 1px 0px #d73905;
} */

.navbar {
  display: flex;
  justify-content: flex-end !important;
  font-size: 1.5rem;
  background-color: #0c3429 !important;
  border: 2px solid #dc7a4a;
  border-radius: 5px;
  box-shadow: 5px 5px 0 rgba(0, 0, 0, 0.505);
  width: 100%;
}
.navbar a {
  display: flex;
  justify-content: flex-end;
  color: #dc7a4a;
  font-size: 2rem;
  text-shadow: 2px 2px 1px black;
}
.navbar a:focus {
  color: #dc7a4a;
  text-shadow: 2px 2px 1px black;
}
.navbar a:hover {
  color: #d73905;
}
.navbar-nav {
  display: flex;
  justify-content: flex-end !important;
}
.navbar-toggler-icon {
  background-color: #dc7a4a;
  border-radius: 20px;
  padding-top: 4px;
  font-weight: bold;
}

.contact-links {
  color: #dc7a4a;
  font-weight: bold;
  text-shadow: -1px 1px 2px black;
  font-size: 2rem;
  letter-spacing: 0.0375rem;
  text-decoration: none;
}
.footer a:focus {
  color: #dc7a4a;
}
.footer a:hover {
  color: #d73905;
  text-decoration: none;
}
.footer {
  background-color: #0c3429;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 100px;
  padding-top: 5px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border-top: 2px solid #dc7a4a;
  text-align: center;
}
.footer a {
  padding: 10px;
}

.projects-header {
  color: #0c3429;
  text-shadow: -2px -2px 8px#dc7a4a, 2px 2px 8px#dc7a4a;
  padding-top: 20px;
  font-size: 5.5rem;
  text-align: center;
  margin-bottom: 2rem;
}

.projects-container {
  color: #dc7a4a;
  font-size: 1.5rem;
  text-shadow: 1px 3px 1px black;
  text-align: center;
}
.project-card {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border: 2px solid #dc7a4a !important;
  border-radius: 10px;
  box-shadow: 6px 8px 3px rgba(0, 0, 0, 0.61);
  margin-bottom: 5px;
  background-color: #dc7a4a;
  justify-content: center;
}
.card-body {
  background-color: #0c3429;
  border-top: 2px solid black;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}
.card-title {
  font-size: 2rem;
  color: #d73905;
  font-size: 2rem;
  text-shadow: 1px 3px 1px black;
}
img {
  padding: 5px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 250px;
}
.btn {
  color: #dc7a4a;
  font-size: 1.25rem;
  font-weight: bold;
  text-shadow: 1px 2px 1px #0c3429;
  background-color: #752f2f;
  border: 2px solid #dc7a4a;
  border-radius: 10px;
  box-shadow: 3px 4px 3px rgba(0, 0, 0, 0.61);
}

.about-header {
  color: #0c3429;
  text-shadow: -2px -2px 8px#dc7a4a, 2px 2px 8px#dc7a4a;
  padding: 20px;
  font-size: 5.5rem;
  text-align: center;
  margin-bottom: 2rem;
}
.about-me-content {
  color: white;
  font-size: 1.5rem;
  text-shadow: 2px 1px 2px rgba(0, 0, 0, 0.603);
  text-align: left;
}
.img-thumbnail {
  width: 35%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  float: right;
  border: 8px solid #0c3429;
  margin-bottom: 1.5rem;
  margin-left: 1.5rem;
  margin-right: 1.5rem;
  background-blend-mode: soft-light;
}
.logo {
  background-color: #0c3429;
  border-radius: 100px;
  border: 4px solid #dc7a4a;
  margin: auto;
  padding-right: 30px;
}

@media only screen and (max-width: 576px) {
  .img-thumbnail {
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    float: right;
    border: 8px solid #0c3429;
    margin-bottom: 1.5rem;
    margin-left: 1.5rem;
    margin-right: 1.5rem;
    background-blend-mode: soft-light;
  }
}

.resume-header {
  color: #0c3429;
  text-shadow: -2px -2px 8px#dc7a4a, 2px 2px 8px#dc7a4a;
  padding: 20px;
  font-size: 5.5rem;
  text-align: center;
}
.resume-download {
  color: white;
  font-size: 2rem;
  text-shadow: 2px 1px 2px rgba(0, 0, 0, 0.603);
  text-align: center;
  background-color: #0c3429;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  padding: 1rem;
  border: 2px solid #dc7a4a;
  border-radius: 8px;
}
.coding-icons {
  color: #0c3429;
  font-size: 2rem;
  font-weight: bold;
  text-shadow: -1px 1px 2px #dc7a4a;
  text-align: center;
}
.icons {
  color: #dc7a4a;
  font-size: 3.5rem;
  text-shadow: 2px 1px 2px rgba(0, 0, 0, 0.603);
  text-align: center;
  list-style-type: none;
  letter-spacing: 1rem;
}
.resume-link {
  color: #dc7a4a;
}
.resume-download a {
  text-decoration: none;
}
.resume-download a:focus {
  color: #dc7a4a;
}
.resume-download a:hover {
  color: #d73905;
  text-decoration: none;
}

.container {
  text-align: center;
}
.contact-header {
  color: #0c3429;
  text-shadow: -2px -2px 8px#dc7a4a, 2px 2px 8px#dc7a4a;
  padding: 20px;
  font-size: 5.5rem;
  text-align: center;
}
.contact-form-header {
  color: #0c3429;
  font-size: 2rem;
  font-weight: bold;
  text-shadow: 1px 2px 4px#dc7a4a;
  text-align: center;
  margin-top: 20px;
}
.user-email {
  width: 50%;
  height: 50px;
  margin-bottom: 10px;
  border-radius: 5px;
  background-color: white;
}
.textarea {
  width: 50%;
  height: 200px;
  position: relative;
  margin-bottom: 10px;
  overflow: auto;
  border-radius: 5px;
  background-color: rgb(255, 255, 255);
}
.contact-me-links {
  color: #dc7a4a;
  font-weight: bold;
  text-shadow: -1px 1px 2px black;
  font-size: 2rem;
  letter-spacing: 0.0375rem;
  text-decoration: none;
}
.container a:focus {
  color: #dc7a4a;
}
.container a:hover {
  color: #d73905;
  text-decoration: none;
}
.button {
  background-color: #0c3429;
  color: #dc7a4a;
  font-size: 1.5rem;
  text-shadow: -1px 1px 2px black;
  width: 200px;
  height: 50px;
  border: 2px solid #dc7a4a;
  box-shadow: 1px 2px 2px black;
  border-radius: 8px;
  margin-bottom: 30px;
}
.links-container {
  background-color: #0c3429;
  padding: 1.5rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  border: 2px solid #dc7a4a;
  border-radius: 8px;
}

